<template>
  <div class="Post-page">
    <h1>Latest Posts</h1>
    <div class="container">
      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="inputGroup-sizing-default">Message to Post:</span>
        </div>
        <input type="text" @keydown.enter="createPost" class="form-control" id="create-post" v-model="text" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-default">
      </div>
      <button type="submit" class="btn btn-primary" @click="createPost" >Post</button>
      
    </div>
    <p class="error" v-if="error">{{error}}</p>
    <div class="container">
      <div class="card"
        v-for="(post,index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
      >
        <div class="card-body">
           {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}
          <p class="text">{{post.text}}</p>
          <button  class="btn btn-danger" v-on:click="deletePost(post._id)" >Delete</button>
        </div>
      </div>
      
    </div>
    

  </div>
</template>


<script>
import PostService from '../service/PostService';
export default {
  name: "Post",
  data(){
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created(){
    try{
      this.posts = await PostService.getPosts();
    }catch(err){
      this.error = err.message;
    }
  },
  methods:{
    async createPost(){
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
      this.text='';
    },
    async deletePost(id){
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
      
    }
  }
};
</script>